import { VuexModule, Module, Mutation } from 'vuex-module-decorators';

/**
 * UserModule
 */
@Module({
  namespaced: true,
  name: 'user'
})
export class UserModule extends VuexModule {
  public id!: number;
  public avatar!: string;
  public google_id!: number;
  public name!: string;
  public email!: string;
  public mobile!: string;
  public gender!: string;
  public birthdate!: string;
  public created_at!: string;
  public updated_at!: string;

  @Mutation
  public updateUserState(newState: { [key: string]: any }) {
    const { id, avatar, google_id, name, email, mobile } = newState;
    [this.id, this.name, this.avatar, this.google_id, this.email, this.mobile] = [id, name, avatar, google_id, email, mobile];

    const { birthdate, created_at, updated_at, gender } = newState;
    [this.birthdate, this.created_at, this.updated_at, this.gender] = [birthdate, created_at, updated_at, gender];
  }
}
