import { VuexModule, Module, Mutation } from 'vuex-module-decorators';

/**
 * ProgressBarModule
 */
@Module({
  name: 'progress-bar'
})
export class ProgressBarModule extends VuexModule {
  public _state = true;

  @Mutation
  public toggleProgressBarState() {
    this._state = !this._state;
  }
}

/**
 * SnackBarModule
 */
@Module({
  name: 'snack-bar'
})
export class SnackBarModule extends VuexModule {
  public _state = {};

  @Mutation
  public toggleSnackBarState(newState: { [key: string]: any }) {
    this._state = newState;
  }
}
